                            padding-left: 25px;
                            position: relative;
                            &:after {
                                content: "";
                                left: 0;
                                position: absolute;
                                width: 14px;
                                height: 14px;
                                border: 1px solid green;
                                top: 2px;
                            }
                            &:before {
                                content: "";
                                left: 3px;
                                width: 8px;
                                height: 8px;
                                background-color: green;
                                position: absolute;
                                border-radius: 100%;
                                top: 5px;
                            }
                        }
                    }
                    &.non-veg {
                        h6 {
                            padding-left: 25px;
                            position: relative;
                            &:after {
                                content: "";
                                left: 0;
                                position: absolute;
                                width: 14px;
                                height: 14px;
                                border: 1px solid red;
                                top: 2px;
                            }
                            &:before {
                                content: "";
                                left: 3px;
                                width: 8px;
                                height: 8px;
                                background-color: red;
                                position: absolute;
                                border-radius: 100%;
                                top: 5px;
                            }
                        }
                    }
                }
            }
            .cart-bottom {
                margin-top: 30px;
                padding-top: 20px;
                border-top: 1px solid $inner-bg;
                .sub-total {
                    font-weight: 700;
                    font-size: 18px;
                    text-transform: capitalize;
                    margin-bottom: 5px;
                    span {
                        float: right;
                    }
                }
                p {
                    font-size: 12px;
                    line-height: 1;
                    margin-bottom: 0;
                    max-width: 70%;
                }
                .checkout {
                    margin-top: 20px;
                }
            }
            .empty-cart {
                text-align: center;
                margin-top: 50px;
                h5 {
                    text-transform: capitalize;
                    color: rgba(0, 0, 0, 0.5);
                    font-size: 16px;
                    margin-bottom: 20px;
                }
                img {
                    margin-bottom: 20px;
                    opacity: 0.5;
                }
                p {
                    font-size: 14px;
                    line-height: 18px;
                    text-align: left;
                    color: rgba(0, 0, 0, 0.64);
                }
            }
        }
    }
}

